* {
  padding: 0;
  margin: 0; }

html {
  font-size: 62.5%; }

body {
  font-family: 'M PLUS Rounded 1c', sans-serif;
  font-size: 1.6rem;
  color: #2A2400; }
  @media only screen and (max-width: 27.5em) {
    body {
      font-size: 1.5rem; } }

textarea,
input,
button,
select {
  font-family: inherit;
  font-size: 1.5rem; }

input:required,
textarea:required {
  box-shadow: none; }

a {
  text-decoration: none;
  color: #2A2400; }
  a:hover, a:active {
    color: #aa9100; }

h1,
h2,
h3,
h4 {
  font-family: 'Kaushan Script', cursive; }

h1 {
  font-size: 5rem; }
  @media only screen and (max-width: 27.5em) {
    h1 {
      font-size: 4rem; } }

h2 {
  font-size: 2.5rem; }

label {
  font-size: 1.4rem; }

.wsdm-tooltip:after {
  content: '';
  position: absolute;
  width: 0;
  height: 0;
  top: 100%;
  left: 50%;
  margin-left: -0.5rem;
  border-left: 0.5rem solid transparent;
  border-right: 0.5rem solid transparent;
  border-top: 0.5rem solid #baa5c1; }

.intro {
  background-color: #c8c561;
  width: 100vw;
  height: 100vh; }
  @supports (background-blend-mode: difference) {
    .intro {
      background: url("../images/hero.jpg") no-repeat center center;
      background-size: cover;
      background-blend-mode: difference;
      background-color: #ede68a; } }
  .intro__arrow:hover {
    color: #2A2400; }
  .intro__arrow svg {
    height: 5rem;
    width: 5rem;
    position: absolute;
    bottom: 12%;
    left: 50%;
    cursor: pointer;
    transform: translateX(-50%);
    transition: transform 0.2s ease; }
    .intro__arrow svg:hover {
      transform: translate(-50%, -1rem); }
  .intro__title {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-align: center; }
    .intro__title h1 {
      cursor: default;
      letter-spacing: 2rem; }
      @media only screen and (max-width: 33.125em) {
        .intro__title h1 {
          letter-spacing: 1rem; } }
      .intro__title h1 span:hover {
        color: #5d5000; }
    .intro__title h2 {
      letter-spacing: 1rem; }

.header {
  position: fixed;
  width: 100%;
  background-color: #2A2400;
  color: #b0a565;
  z-index: 1000; }
  .header a {
    color: #b0a565; }
    .header a:hover, .header a:active {
      color: #dbd6b9; }
  .header__container {
    display: flex;
    width: 80%;
    justify-content: space-between;
    height: 5rem;
    align-items: center;
    margin: auto; }
    @media only screen and (max-width: 27.5em) {
      .header__container {
        justify-content: flex-end; } }
  @media only screen and (max-width: 27.5em) {
    .header__title {
      display: none; } }
  .header__nav a {
    margin-left: 2rem; }

.about {
  background: linear-gradient(180deg, #c8c561 0%, #E7F5C1 100%);
  overflow: auto;
  text-align: center; }
  .about > * {
    margin: auto;
    width: 80%; }
  .about a {
    text-decoration: underline; }
  .about__title {
    padding: 2rem 0 3rem 0; }
  .about__description {
    margin-bottom: 3rem; }
    .about__description p {
      padding-bottom: 2rem;
      line-height: 3rem; }
  .about__subtitle--container {
    display: flex;
    color: #4C5100; }
  .about__subtitle--item {
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #2A2400; }
    .about__subtitle--item:first-child > h2 {
      margin-left: auto;
      font-size: 3.5rem; }
      @media only screen and (max-width: 27.5em) {
        .about__subtitle--item:first-child > h2 {
          margin-left: unset; } }
    .about__subtitle--item:nth-child(2) {
      flex-grow: 0;
      color: #4C5100; }
      @media only screen and (max-width: 27.5em) {
        .about__subtitle--item:nth-child(2) svg {
          display: none; } }
    .about__subtitle--item:last-child > h2 {
      margin-right: auto;
      font-size: 3.5rem; }
  .about__worldly-icon {
    height: 10rem;
    width: 10rem;
    margin: 0 3.5rem; }

.timeline {
  background: linear-gradient(180deg, #E7F5C1 0%, #c8b461 100%);
  padding-bottom: 3rem;
  text-align: center; }
  .timeline > * {
    margin: auto;
    width: 80%; }
  .timeline__title {
    padding: 10rem 0 3rem 0; }
  .timeline .vertical-timeline {
    margin-top: 0;
    font-size: 1.5rem !important; }
    @media only screen and (max-width: 1170px) {
      .timeline .vertical-timeline {
        width: 85%; } }
    .timeline .vertical-timeline:before {
      background: #2A2400; }
    .timeline .vertical-timeline-element:last-of-type .vertical-timeline-element-content {
      display: none; }
    .timeline .vertical-timeline-element-content {
      background-color: #E2EC9A;
      box-shadow: 0 0.1rem 0 0.1rem rgba(0, 0, 0, 0.08);
      border: 0.2rem solid #2A2400; }
      .timeline .vertical-timeline-element-content .vertical-timeline-element-date {
        font-size: 1.5rem; }
        @media only screen and (max-width: 1170px) {
          .timeline .vertical-timeline-element-content .vertical-timeline-element-date {
            font-size: 1.2rem;
            padding: .4rem 0;
            float: none;
            font-weight: 700; } }
      .timeline .vertical-timeline-element-content p {
        font-size: 1.5rem !important;
        margin: 0; }
      .timeline .vertical-timeline-element-content a {
        text-decoration: underline; }
    .timeline .vertical-timeline-element-icon {
      box-shadow: none;
      width: 5rem;
      height: 5rem;
      margin-left: -2.5rem;
      margin-top: 1rem; }
      @media only screen and (max-width: 1170px) {
        .timeline .vertical-timeline-element-icon {
          width: 4.5rem;
          height: 4.5rem;
          margin-left: -.3rem;
          margin-top: .5rem; } }
    .timeline .vertical-timeline-element--left .vertical-timeline-element-date {
      text-align: left; }
    .timeline .vertical-timeline-element--left .vertical-timeline-element-content::before {
      border-left-color: #2A2400 !important; }
    @media only screen and (max-width: 1170px) {
      .timeline .vertical-timeline-element--left .vertical-timeline-element-content::before {
        border-right-color: #2A2400 !important;
        border-left-color: transparent !important; } }
    .timeline .vertical-timeline-element--right .vertical-timeline-element-content::before {
      border-right-color: #2A2400 !important; }

.contact {
  padding: 6rem 0;
  background: linear-gradient(180deg, #c8b461 0%, #FFE7BC 100%);
  text-align: center; }
  .contact > * {
    margin: auto;
    width: 80%; }
  .contact__form {
    margin: 5rem auto; }
    .contact__form form {
      display: grid;
      grid-template-columns: repeat(4, 1fr);
      grid-row-gap: 2.5rem;
      grid-column-gap: 2.5rem; }
    .contact__form--label {
      position: absolute;
      left: 0;
      font-size: 1.2rem;
      margin-left: 1rem;
      transition: all .3s ease-out;
      z-index: 1; }
    .contact__form--details {
      grid-column-end: span 2;
      position: relative; }
      @media only screen and (max-width: 33.125em) {
        .contact__form--details {
          grid-column-end: span 4; } }
      .contact__form--details input {
        height: 4rem;
        padding: 0 1rem;
        box-sizing: border-box;
        width: 100%;
        outline: none;
        border: 1px solid #4C5100;
        transition: all 0.3s ease-in-out;
        position: relative;
        z-index: 2; }
        .contact__form--details input:focus {
          border: 1px solid #4C5100;
          box-shadow: 0 0 0.5rem #4C5100; }
          .contact__form--details input:focus ~ .contact__form--label {
            margin-top: -2rem; }
        .contact__form--details input::placeholder {
          font-family: 'M PLUS Rounded 1c', sans-serif; }
    .contact__form--message {
      grid-column-end: span 4;
      position: relative; }
      .contact__form--message textarea {
        min-height: 4rem;
        padding: 1rem;
        box-sizing: border-box;
        width: 100%;
        outline: none;
        border: 1px solid #4C5100;
        transition: all 0.3s ease-in-out;
        position: relative;
        z-index: 2; }
        .contact__form--message textarea:focus {
          border: 1px solid #4C5100;
          box-shadow: 0 0 0.5rem #4C5100; }
          .contact__form--message textarea:focus ~ .contact__form--label {
            margin-top: -2rem; }
    .contact__form--result {
      grid-column-end: span 3;
      grid-row-start: 3;
      align-self: center;
      font-size: 1.4rem; }
      .contact__form--result.true {
        color: green; }
      .contact__form--result.false {
        color: red; }
      @media only screen and (max-width: 33.125em) {
        .contact__form--result {
          grid-row-start: 5;
          grid-column-end: span 4;
          margin: 1rem 2rem;
          height: 6rem; } }
    .contact__form--submit {
      grid-column-start: 4;
      height: 4rem;
      border: none;
      background-color: #4C5100;
      color: #ede68a;
      cursor: pointer; }
      .contact__form--submit:hover {
        background-color: #646b00; }
      .contact__form--submit:disabled {
        cursor: not-allowed;
        background-color: #949e00; }
      @media only screen and (max-width: 33.125em) {
        .contact__form--submit {
          grid-column-start: 3;
          grid-column-end: span 2; } }
      @media only screen and (max-width: 27.5em) {
        .contact__form--submit {
          grid-column-start: 1;
          grid-column-end: span 4; } }
  .contact__icons {
    display: flex;
    justify-content: center; }
    .contact__icons svg {
      height: 5rem;
      width: 5rem; }
      .contact__icons svg:hover {
        color: #5d5000;
        cursor: pointer; }
    .contact__icons a {
      display: inline-block;
      height: 48px;
      width: 48px;
      vertical-align: middle;
      margin: 0 1.5rem; }
    @media only screen and (max-width: 27.5em) {
      .contact__icons {
        display: inline-block; }
        .contact__icons--top {
          margin-bottom: 3rem; } }

.world-map {
  margin: 5rem auto; }
  @media only screen and (max-width: 33.125em) {
    .world-map {
      width: 95%; } }
  .world-map .rsm-marker {
    fill: #FB97C6; }
  .world-map__legend {
    display: flex;
    justify-content: space-around;
    padding: 1rem 0; }
    @media only screen and (max-width: 27.5em) {
      .world-map__legend {
        display: flex;
        flex-direction: column;
        align-items: center; }
        .world-map__legend > * {
          margin-bottom: 1rem; } }
    .world-map__legend > * {
      display: flex;
      align-items: center; }
    .world-map__legend span {
      display: inline-block;
      height: 1.4rem;
      width: 1.4rem;
      margin-right: .8rem; }
    .world-map__legend--cities span {
      background-color: #FB97C6; }
    .world-map__legend--lived span {
      background-color: #3A024E; }
    .world-map__legend--visited span {
      background-color: #6E2F85; }
